import React, {Component} from 'react';

export default class Header extends Component
{
    constructor(props)
    {
        super(props);

        this.state = {
            visb: false
        }
        this.onClickButton = this.onClickButton.bind(this);
    }
    render()
    {
        if(!this.state.visb)
            return (
                <header>
                    <button onClick={this.onClickButton}>{this.props.but}</button>
                </header>
            );
        else
            return (
                <header>
                    <div>Ты меня видишь!</div>
                    <button onClick={this.onClickButton}>Click me lol!</button>
                </header>
            );
    }


    onClickButton()
    {
        this.setState(state => ({
            visb: !this.state.visb
        }));
    }
}